<template>
    <div class="container mt-5 text-center">
        <h1 class="text-center">Weather Portal</h1>
        <div class="input-group mt-5">
            <input id="regionQuerySearch" v-model="regionQuerySearch" class="form-control" type="text" placeholder="Type your region">
            <div class="input-group-append"><button class="btn btn-primary" v-on:click="getWeatherData">View Weather</button></div>
        </div>
        <div v-if="weatherList.length == 0">
            
        </div>
        <div v-else>
            <div class="jumbotron mt-5">
                <h2>{{weatherList.location.name}}, {{weatherList.location.region}}, {{weatherList.location.country}}</h2>
                <h3>{{weatherList.current.weather_descriptions[0]}}</h3>
                <div class="row mt-5">
                    <div class="col-4">
                        <h3>Temperature</h3>
                        <h4>{{weatherList.current.temperature}}</h4>
                    </div>
                    <div class="col-4">
                        <h3>Wind Speed</h3>
                        <h4>{{weatherList.current.wind_speed}}</h4>
                    </div>
                    <div class="col-4">
                        <h3>UV Index</h3>
                        <h4>{{weatherList.current.uv_index}}</h4>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'WeatherApp',
    regionQuerySearch: String,
    data() {
        return{
            weatherList: []
        };
    },
    methods: {
        getWeatherData(){
            axios.get("http://api.weatherstack.com/current?access_key=8d3fd9354d47510287bb32c032277bbe&query=" + this.regionQuerySearch).then(res => (this.weatherList = res.data));
        }
    }
}
</script>